@charset "UTF-8";
/*
  * Reset CSS
*/
/* Box sizing rules */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */
ul[role=list],
ol[role=list] {
  list-style: none;
}

/* Set core root defaults */
html:focus-within {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizespeed;
  line-height: 1.5;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img,
picture {
  max-width: 100%;
  display: block;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* =====================
 * リセットCSS以外のデフォルトスタイルを調整
===================== */
.grecaptcha-badge {
  visibility: hidden;
}

img {
  max-width: 100%;
  height: auto;
  vertical-align: top;
}

a {
  display: inline-block;
  color: inherit;
  cursor: pointer;
  text-decoration: none;
}
@media (min-width: 768px) {
  a:hover, a:focus {
    opacity: 0.7;
  }
}

ul,
li {
  padding: 0;
  margin: 0;
  list-style: none;
}

button {
  background: none;
  border: none;
  outline: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}
@media (min-width: 768px) {
  button:hover, button:focus {
    opacity: 0.7;
  }
}

input[type=text],
textarea {
  appearance: none;
}

input,
textarea {
  appearance: none;
  border-radius: 0;
}

/* =====================
 * Baseスタイル
===================== */
:root,
:root[theme=Default] {
  --f-family-main: "游ゴシック体", "YuGothic", "Yu Gothic", "メイリオ", "Meiryo",
    "Hiragino Kaku Gothic ProN", "ヒラギノ角ゴ ProN W3", sans-serif;
  --f-family-sub: "oswald", sans-serif;
  --f-font-size: 16px;
  --f-line-height: 1.6;
  --f-weight-regular: 400;
  --f-weight-medium: 500;
  --f-weight-semibold: 600;
  --f-weight-bold: 700;
  --f-weight-black: 900;
  --c-main: #ffffff;
  --c-sub: #272727;
  --c-sub-rgb-opacity25: rgba(39, 39, 39, 0.25);
  --c-sub-rgb-opacity40: rgba(39, 39, 39, 0.4);
  --c-sub-rgb-opacity60: rgba(39, 39, 39, 0.6);
  --c-red: #ff0303;
  --c-accent: #cccccc;
  --c-accent-opacity-40: rgba(202, 202, 202, 0.4);
  --c-gray: #8c8c8c;
  --c-green: #08aa9f;
  --c-twitter: #000000;
  --c-github: #1a1414;
}

html {
  font-size: 16px;
  /* Fix Safari bug with viewport units in calc() */
  min-height: 0;
}
@media (min-width: 768px) {
  html {
    font-size: clamp(12px, 12px + 0.0147058824 * (100vw - 768px), 16px);
  }
}

body {
  font-family: var(--f-family-main);
  font-size: var(--f-font-size);
  font-weight: var(--f-weight-medium);
  line-height: var(--f-line-height);
  background-color: var(--c-main);
  color: var(--c-sub);
  word-break: break-all;
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.3s;
}

body#loaded {
  visibility: visible;
  opacity: 1;
}

.l-container {
  width: 100%;
  margin: 0 auto;
  padding: 0 1.75rem;
}
@media (min-width: 768px) {
  .l-container {
    max-width: 71rem;
    padding: 0 3rem;
  }
}

.l-footer {
  text-align: center;
  padding: 1.5rem 0;
  width: 100%;
}
@media (min-width: 768px) {
  .l-footer {
    padding: 1rem 0;
  }
}

.l-footer__contents {
  width: 100%;
  height: 100%;
}
@media (min-width: 768px) {
  .l-footer__contents {
    display: flex;
    align-items: center;
    justify-content: flex-start;
  }
}

.l-footer__logo {
  margin-left: auto;
  margin-right: auto;
}
@media (min-width: 768px) {
  .l-footer__logo {
    margin-left: initial;
    margin-right: initial;
  }
}

.l-footer__nav {
  margin-top: 2rem;
}
@media (min-width: 768px) {
  .l-footer__nav {
    margin-top: initial;
    margin-left: 2.5rem;
  }
}

.l-footer__sns {
  text-align: center;
  margin-top: 2rem;
  border-top: 1px solid var(--c-accent);
  width: 100%;
  justify-content: center;
  padding-top: 1.5rem;
}
@media (min-width: 768px) {
  .l-footer__sns {
    margin-top: initial;
    margin-left: auto;
    border-top: none;
    width: initial;
    border-left: 1px solid var(--c-accent);
    padding: 0.75rem 0 0.75rem 1.5rem;
  }
}

.l-footer__copyright {
  display: inline-block;
  margin-top: 2rem;
}
@media (min-width: 768px) {
  .l-footer__copyright {
    display: block;
    margin-left: auto;
    text-align: right;
  }
}

.l-header {
  width: 100%;
  text-align: center;
  background-color: var(--c-main);
  border-bottom: 1px solid var(--c-accent);
  box-shadow: 0 1px 4px -1px var(--c-sub-rgb-opacity25);
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1000;
}

.l-header__container {
  display: flex;
  align-items: center;
  width: 100%;
  height: 100%;
  min-height: 64px;
}

.l-header__hamburger {
  margin-left: auto;
}

.l-header.is-menu-open {
  box-shadow: none;
  border-bottom: none;
}

.l-main {
  text-align: center;
}

.l-sidebar {
  width: 100%;
  z-index: 970;
}
@media (min-width: 768px) {
  .l-sidebar {
    position: sticky;
    height: 100%;
    max-width: 18.75rem;
  }
}

.l-sidebar__title {
  font-size: 1.25rem;
  font-weight: var(--f-weight-bold);
  width: 100%;
  line-height: 1;
  padding: 0.875rem 1rem;
  background-color: var(--c-accent-opacity-40);
}

.l-sidebar__title:not(:first-child) {
  margin-top: 2rem;
}

.l-sidebar__categories,
.l-sidebar__tags {
  margin-top: 1.5rem;
}

.c-button-line {
  display: inline-block;
  min-width: 15rem;
  background-color: var(--c-sub-rgb-opacity60);
  color: var(--c-main);
  padding: 1.125rem 3rem 1.125rem 3rem;
  font-size: 1rem;
  font-weight: var(--f-weight-bold);
  letter-spacing: 0.04em;
  position: relative;
  transition: all 0.3s;
  text-align: center;
  line-height: 1;
}
@media (min-width: 768px) {
  .c-button-line:hover {
    opacity: 0.8;
  }
}

@media (min-width: 768px) {
  .c-button-line:hover .c-button-line__text,
.c-button-line:focus .c-button-line__text {
    transform: translateX(-1em);
  }
}

.c-button-line__text {
  display: inline-block;
  position: relative;
  z-index: 0;
  transition: all 0.3s;
}

.c-button-line__line {
  display: inline-block;
  width: 2.5rem;
  height: 2px;
  background-color: var(--c-main);
  transition: height 0.3s;
  position: absolute;
  top: 50%;
  right: 0;
  transform: translate(0, -50%);
}

.c-button-scroll {
  display: inline-flex;
  align-items: center;
  background-color: var(--c-main);
  border: 1px solid var(--c-gray);
  padding: 0.25rem 0.375rem;
  position: fixed;
  bottom: 0.625rem;
  right: 0.625rem;
  z-index: 980;
}
@media (min-width: 768px) {
  .c-button-scroll {
    display: none;
  }
}

.c-button-text {
  display: inline-block;
  writing-mode: vertical-rl;
  line-height: 1;
  font-size: 0.875rem;
  font-weight: var(--f-weight-bold);
}

.c-button-scroll__icon {
  display: inline-block;
  width: 1rem;
  margin-left: 0.25rem;
}

.c-button-submit {
  text-align: center;
  position: relative;
  display: inline-block;
  align-self: center;
}

.c-button-submit:hover,
.c-button-submit:focus {
  cursor: pointer;
}
@media (min-width: 768px) {
  .c-button-submit:hover,
.c-button-submit:focus {
    cursor: pointer;
    opacity: 0.8;
  }
  .c-button-submit:hover .c-button-submit__text,
.c-button-submit:focus .c-button-submit__text {
    left: calc(50% - 1em);
  }
}

.c-button-submit .wpcf7-spinner {
  display: none;
}

.c-button-submit input[type=submit] {
  display: inline-block;
  min-width: 15rem;
  min-height: 3.25rem;
  background-color: var(--c-sub-rgb-opacity60);
  border: none;
  color: var(--c-main);
  padding: 1.125rem 3rem 1.125rem 3rem;
  font-weight: var(--f-weight-bold);
  letter-spacing: 0.04em;
  position: relative;
  transition: all 0.3s;
  text-align: center;
  line-height: 1;
  font-size: 0;
}

.c-button-submit__text {
  display: inline-block;
  font-size: 1rem;
  color: var(--c-main);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-weight: var(--f-weight-bold);
  letter-spacing: 0.04em;
  transition: left 0.3s;
  pointer-events: none;
}

.c-button-submit__line {
  display: inline-block;
  width: 2.5rem;
  height: 2px;
  background-color: var(--c-main);
  transition: height 0.3s;
  position: absolute;
  top: 50%;
  right: 0;
  transform: translate(0, -50%);
  pointer-events: none;
}

.c-button-submit input[type=submit]:disabled {
  background-color: var(--c-main);
  border: 2px solid var(--c-gray);
  cursor: not-allowed;
}
.c-button-submit input[type=submit]:disabled ~ .c-button-submit__text {
  color: var(--c-gray);
  cursor: not-allowed;
}
.c-button-submit input[type=submit]:disabled ~ .c-button-submit__line {
  background-color: var(--c-gray);
  cursor: not-allowed;
}

.c-sns__twitter {
  mask-image: url("/wordpress/wp-content/themes/portfolio_n/assets/images/twitter_logo_black.svg");
  background-color: var(--c-twitter);
  mask-repeat: no-repeat;
  mask-position: center;
}

.c-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 18.75rem;
}
.c-card:not(:first-child) {
  margin-top: 2.5rem;
}
@media (min-width: 768px) {
  .c-card {
    max-width: 33.3333%;
    width: 100%;
  }
  .c-card:not(:first-child) {
    margin-top: initial;
  }
  .c-card:not(:nth-child(3n+1)) {
    margin-left: 2.5rem;
  }
}

.c-card__image {
  width: 5rem;
  height: auto;
}

.c-card__title {
  font-size: 1.25rem;
  font-weight: var(--f-weight-bold);
  line-height: 1.6;
  color: var(--c-sub);
  margin-top: 1rem;
  text-align: center;
}

.c-card__title span {
  display: inline-block;
}

.c-card__head {
  text-align: center;
  width: 100%;
  padding-bottom: 1rem;
  color: var(--c-gray);
  font-weight: var(--f-weight-bold);
  border-bottom: 1px solid var(--c-accent);
  margin-top: 1.5rem;
}

.c-card__item {
  font-size: 1rem;
}

.c-card__body {
  font-size: 1rem;
  font-weight: var(--f-weight-medium);
  color: var(--c-sub);
  margin-top: 1rem;
}
@media (min-width: 768px) {
  .c-card__body {
    flex-grow: 1;
  }
}

.c-cards {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .c-cards {
    flex-direction: row;
  }
}

.c-categories {
  display: flex;
  flex-wrap: wrap;
  column-gap: 1rem;
  row-gap: 1.25rem;
  min-width: 8.75rem;
}
@media (min-width: 768px) {
  .c-categories {
    flex-wrap: nowrap;
    flex-direction: column;
  }
}

.c-category {
  display: flex;
  line-height: 1;
  width: max(50% - 1rem, 7.625rem);
  max-width: 12.5rem;
  font-weight: var(--f-weight-bold);
  border: 1px solid var(--c-sub);
}
@media (min-width: 768px) {
  .c-category {
    width: 100%;
  }
}

.c-category.is-current {
  background-color: var(--c-gray);
  color: var(--c-main);
  border: none;
}

.c-category__number {
  display: inline-block;
  font-size: 0.875rem;
  writing-mode: vertical-rl;
  text-orientation: upright;
  color: var(--c-main);
  background-color: var(--c-gray);
  text-align: center;
}

.c-category__title {
  display: inline-block;
  padding: 0.75rem 0.75rem 0.75rem 1rem;
  font-size: 1rem;
  align-self: center;
}

.c-copyright {
  font-size: 0.875rem;
  line-height: 1;
  text-transform: uppercase;
  color: var(--c-sub-rgb-opacity60);
}

.c-form-part {
  width: min(100%, 33.75rem);
  font-size: max(1rem, 16px);
}

.c-form-part__head {
  display: flex;
  align-items: center;
  line-height: 1;
}

.c-form-part__label {
  display: inline-flex;
  flex-direction: column;
  width: 100%;
}

.c-form-part__required,
.c-form-part__optional {
  display: inline-flex;
  align-items: center;
  font-size: max(0.875rem, 14px);
  font-weight: var(--f-weight-medium);
  min-width: 2.5rem;
}
.c-form-part__required::before,
.c-form-part__optional::before {
  content: "";
  display: inline-block;
  width: 0.5rem;
  height: 0.5rem;
  margin-right: 0.25rem;
}

.c-form-part__required::before {
  background-color: var(--c-red);
}

.c-form-part__optional::before {
  background-color: var(--c-sub-rgb-opacity60);
}

.c-form-part__name {
  font-size: 1rem;
  line-height: 1;
  font-weight: var(--f-weight-bold);
  margin-left: 0.75rem;
}

.c-form-part__input {
  padding: 0.5rem 0.75rem;
  min-height: 48px;
  margin-top: 1rem;
  border: 1px solid var(--c-sub-rgb-opacity60);
  width: 100%;
}

.c-form-part__textarea {
  min-height: 25rem;
  margin-top: 1rem;
  padding: 0.5rem 0.75rem;
  border: 1px solid var(--c-sub-rgb-opacity60);
  width: 100%;
}

.c-form-part__input:focus, .c-form-part__input:active,
.c-form-part__textarea:focus,
.c-form-part__textarea:active {
  outline: 1px solid var(--c-green);
  box-shadow: 0 0 6px var(--c-green);
}
.c-form-part__input::placeholder,
.c-form-part__textarea::placeholder {
  color: var(--c-gray);
}

.c-form-part .wpcf7-not-valid {
  border-color: var(--c-red);
}

.c-form-part .wpcf7-not-valid-tip {
  color: var(--c-red);
  margin-top: 0.5rem;
  display: inline-flex;
  align-items: center;
}

.c-form-part .wpcf7-not-valid-tip::before {
  content: "";
  display: inline-block;
  width: 1rem;
  height: 1px;
  background-color: var(--c-red);
  margin-right: 0.5rem;
}

.c-hamburger {
  width: 40px;
  height: 40px;
  border: 1px solid var(--c-sub-rgb-opacity60);
  padding: 12px 4px;
}
@media (min-width: 768px) {
  .c-hamburger {
    display: none;
  }
}

.c-hamburger__container {
  display: flex;
  position: relative;
  width: 100%;
  height: 100%;
}

.c-hamburger__line {
  position: absolute;
  display: inline-block;
  width: 100%;
  height: 1px;
  background-color: var(--c-sub);
  transition: transform 0.2s, opacity 0.2s;
}

.c-hamburger__line-top {
  top: 0;
}

.c-hamburger__line-mid {
  top: 0;
  bottom: 0;
  margin: auto;
}

.c-hamburger__line-bot {
  bottom: 0;
}

.c-hamburger.is-active .c-hamburger__line-top {
  transform: rotate(40deg);
  top: 50%;
}
.c-hamburger.is-active .c-hamburger__line-mid {
  opacity: 0;
}
.c-hamburger.is-active .c-hamburger__line-bot {
  transform: rotate(-40deg);
  top: 50%;
}

